import {
  forwardRef,
  memo,
  Ref,
  useCallback,
  useEffect,
  useImperativeHandle,
  useMemo,
  useRef,
  useState,
} from 'react';
import { useSelector } from 'react-redux';
import {
  TransformWrapper,
  TransformComponent,
  ReactZoomPanPinchRef,
} from 'react-zoom-pan-pinch';
import dynamic from 'next/dynamic';
import { DEFAULT_VALUE_FLOORPLAN_CONFIG } from '@containers/Home/constants';
import { selectListScene } from '../../selectors';
import { ImageFloorplanWrapper } from './styled';
import { I_FloorplanConfig, I_Floorplan, I_Scene } from '../../types';

const Marker = dynamic(() => import('./Marker'));

export type Type_RefFuncImageFloorplan = {
  getInstance: () => ReactZoomPanPinchRef;
};

interface Props {
  zoom?: number;
  maxZoom?: number;
  minZoom?: number;
  sceneSelected?: I_Scene;
  floorplanSelected?: I_Floorplan;
  markerSelected?: I_Floorplan['markers'][number];
  configFloorplan?: I_FloorplanConfig;
  onChangeScene: (scene: I_Scene) => void;
}

const ImageFloorplan = forwardRef(
  (
    {
      configFloorplan,
      floorplanSelected,
      sceneSelected,
      markerSelected,
      onChangeScene,
      zoom = 1.5,
      minZoom = 1,
      maxZoom = 2,
    }: Props,
    ref: Ref<Type_RefFuncImageFloorplan>,
  ) => {
    const [isImageLoaded, setIsImageLoaded] = useState(false);
    const floorplanRef = useRef<ReactZoomPanPinchRef>(null!);

    const listScene = useSelector(selectListScene);

    const configRadar = useMemo(
      () => ({
        ...DEFAULT_VALUE_FLOORPLAN_CONFIG.radar,
        ...configFloorplan?.radar,
      }),
      [configFloorplan?.radar],
    );
    const configMarker = useMemo(
      () => ({
        ...DEFAULT_VALUE_FLOORPLAN_CONFIG.marker,
        ...configFloorplan?.marker,
      }),
      [configFloorplan?.marker],
    );

    useEffect(() => {
      if (markerSelected && isImageLoaded) {
        /**
         * FIXME: setTimeout is a workaround for the bug that the marker is not rendered yet (error node not found)
         */
        setTimeout(() => {
          const scale = floorplanRef.current?.state?.scale || zoom;
          markerSelected.id &&
            floorplanRef.current?.zoomToElement(markerSelected.id, scale);
        }, 200);
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [isImageLoaded, markerSelected]);

    useImperativeHandle(ref, () => ({
      getInstance: () => floorplanRef.current,
    }));

    const onClickMarker = useCallback(
      marker => {
        const scene = listScene.filter(
          item => item.id === marker.target_scene_id,
        )[0];
        if (scene) onChangeScene(scene);
      },
      [listScene, onChangeScene],
    );

    return (
      <TransformWrapper
        centerZoomedOut
        initialScale={zoom}
        ref={floorplanRef}
        wheel={{ disabled: true }}
        pinch={{ disabled: true }}
        doubleClick={{ disabled: true }}
        minScale={minZoom}
        maxScale={maxZoom}
        alignmentAnimation={{
          animationTime: 150,
          animationType: 'linear',
        }}
      >
        <TransformComponent wrapperStyle={{ width: '100%', height: '100%' }}>
          <ImageFloorplanWrapper>
            <img
              src={floorplanSelected?.src}
              alt="test"
              onLoad={() => setIsImageLoaded(true)}
            />
            {floorplanSelected?.markers?.map(marker => (
              <Marker
                key={marker.id}
                marker={marker}
                onClick={onClickMarker}
                configRadar={configRadar}
                configMarker={configMarker}
                northRadar={sceneSelected?.north_ath || 0}
                isTurnOnRadar={!!configRadar.isEnabled}
                isActive={markerSelected?.id === marker.id}
              />
            ))}
          </ImageFloorplanWrapper>
        </TransformComponent>
      </TransformWrapper>
    );
  },
);

export default memo(ImageFloorplan);
