import { RefObject, useCallback, useEffect, useMemo, useState } from 'react';
import { useSelector } from 'react-redux';
import { useTourEventState } from '../global/TourStats';
import {
  selectListFloorplan,
  selectConfigFloorplan,
  selectSceneSelected,
} from '../selectors';
import { I_Floorplan } from '../types';
import { Type_RefFuncImageFloorplan } from '../components/Floorplan/ImageFloorplan';

interface Props {
  floorplanRef: RefObject<Type_RefFuncImageFloorplan>;
}

export const useEventFloorplan = ({ floorplanRef }: Props) => {
  const listFloorplan = useSelector(selectListFloorplan);
  const sceneSelected = useSelector(selectSceneSelected);
  const configFloorplan = useSelector(selectConfigFloorplan);

  const [, setTourEventStats] = useTourEventState('state');

  const [floorplanSelected, setFloorplanSelected] = useState<
    I_Floorplan | undefined
  >(undefined);

  useEffect(() => {
    // if (!floorplanSelected) {
    const fp = listFloorplan
      ? listFloorplan.filter(
          item =>
            item.markers?.filter(
              marker => marker.target_scene_id === sceneSelected?.id,
            ).length > 0,
        )[0] ||
        listFloorplan[0] ||
        undefined
      : undefined;
    setFloorplanSelected(fp);
    // }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [listFloorplan, sceneSelected?.id]);

  const markerSelected = useMemo(
    () =>
      floorplanSelected?.markers?.filter(
        item => item.target_scene_id === sceneSelected?.id,
      )[0] || undefined,
    [floorplanSelected, sceneSelected?.id],
  );

  const configDisplayPanel = useMemo(
    () =>
      configFloorplan?.floorplan?.type === 'panel'
        ? configFloorplan?.floorplan
        : undefined,
    [configFloorplan?.floorplan],
  );

  const configDisplayBox = useMemo(
    () =>
      configFloorplan?.floorplan?.type === 'box'
        ? {
            ...{
              offsetBottom: 48,
              offsetLeft: 10,
              offsetRight: 10,
              offsetTop: 10,
            },
            ...configFloorplan?.floorplan,
          }
        : undefined,
    [configFloorplan?.floorplan],
  );

  const [visiblePopup, setVisiblePopup] = useState(false);

  const onCloseFloorplan = useCallback(() => {
    setTourEventStats(prev => ({ ...prev, isVisibleFloorplan: false }));
  }, [setTourEventStats]);

  const onOpenPopup = useCallback(() => setVisiblePopup(true), []);
  const onClosePopup = useCallback(() => setVisiblePopup(false), []);

  const onZoomIn = useCallback(
    () => floorplanRef.current?.getInstance().zoomIn(),
    [floorplanRef],
  );
  const onZoomOut = useCallback(
    () => floorplanRef.current?.getInstance().zoomOut(),
    [floorplanRef],
  );
  const onCenterToMarkerActive = useCallback(() => {
    const scale = floorplanRef.current?.getInstance().state.scale;
    if (markerSelected?.id)
      floorplanRef.current
        ?.getInstance()
        ?.zoomToElement(markerSelected.id, scale || undefined);
  }, [floorplanRef, markerSelected]);

  const onChangeFloorplan = useCallback(
    ({ type }) => {
      const fp = listFloorplan.filter(item => item.id === type)[0];
      if (fp) setFloorplanSelected(fp);
    },
    [listFloorplan],
  );

  return useMemo(
    () => ({
      visiblePopup,
      sceneSelected,
      listFloorplan,
      markerSelected,
      configFloorplan,
      configDisplayBox,
      floorplanSelected,
      configDisplayPanel,
      onCenterToMarkerActive,
      onChangeFloorplan,
      onCloseFloorplan,
      onClosePopup,
      onOpenPopup,
      onZoomOut,
      onZoomIn,
    }),
    [
      visiblePopup,
      sceneSelected,
      listFloorplan,
      markerSelected,
      configFloorplan,
      configDisplayBox,
      floorplanSelected,
      configDisplayPanel,
      onCenterToMarkerActive,
      onChangeFloorplan,
      onCloseFloorplan,
      onClosePopup,
      onOpenPopup,
      onZoomOut,
      onZoomIn,
    ],
  );
};
