/* eslint-disable no-nested-ternary */
import { memo, useRef, useState } from 'react';
import { I_Scene } from '@containers/Home/types';
import { DEFAULT_VALUE_FLOORPLAN_CONFIG } from '@containers/Home/constants';
import dynamic from 'next/dynamic';
import { useTourEventState } from '@containers/Home/global/TourStats';
import { useEventFloorplan } from '@containers/Home/hook/useEventFloorplan';
import { useSelector } from 'react-redux';
import { selectThemeConfigGlobal } from '@containers/Home/selectors';
import { ActionFloorplanWrapper, FloorplanBoxWrapper } from './styled';
import ImageFloorplan, { Type_RefFuncImageFloorplan } from './ImageFloorplan';

const SIZE_BOX = {
  normal: { width: 220, height: 160 },
  scale: { width: 500, height: 390 },
};

const DropdownContainer = dynamic(
  () => import('@components/DropdownContainer'),
);
const Popup = dynamic(() => import('./Popup'));

interface Props {
  onChangeScene: (scene: I_Scene) => void;
}

const FloorplanBox = memo(({ onChangeScene }: Props) => {
  const floorplanRef = useRef<Type_RefFuncImageFloorplan>(null!);

  const themeConfig = useSelector(selectThemeConfigGlobal);

  const [tourEventStats] = useTourEventState('state');

  const {
    visiblePopup,
    sceneSelected,
    listFloorplan,
    markerSelected,
    configFloorplan,
    configDisplayBox,
    floorplanSelected,
    onCenterToMarkerActive,
    onChangeFloorplan,
    onCloseFloorplan,
    onClosePopup,
    onOpenPopup,
    onZoomOut,
    onZoomIn,
  } = useEventFloorplan({
    floorplanRef,
  });

  const isActionAreaOverRef = useRef(false);
  const [sizeBox, setSizeBox] = useState(SIZE_BOX.normal);

  return (
    <FloorplanBoxWrapper
      bgColor={
        configFloorplan?.bgColor || DEFAULT_VALUE_FLOORPLAN_CONFIG.bgColor
      }
      position={configDisplayBox.position || 'bottomRight'}
      offsetTop={configDisplayBox.offsetTop || 10}
      offsetBottom={configDisplayBox.offsetBottom || 10}
      offsetRight={configDisplayBox.offsetRight || 10}
      offsetLeft={configDisplayBox.offsetLeft || 10}
      onMouseOver={() => {
        setSizeBox(SIZE_BOX.scale);
        if (!isActionAreaOverRef.current && sizeBox === SIZE_BOX.normal) {
          setTimeout(() => {
            const scale = floorplanRef.current?.getInstance().state.scale;
            if (markerSelected)
              floorplanRef.current
                ?.getInstance()
                ?.zoomToElement(markerSelected.id, scale);
            else floorplanRef.current?.getInstance()?.resetTransform();
          }, 200);
        }
      }}
      onMouseLeave={() => {
        setSizeBox(SIZE_BOX.normal);
        setTimeout(() => {
          if (!isActionAreaOverRef.current) {
            const scale = floorplanRef.current?.getInstance().state.scale;
            if (markerSelected)
              floorplanRef.current
                ?.getInstance()
                ?.zoomToElement(markerSelected.id, scale);
            else floorplanRef.current?.getInstance()?.resetTransform();
          }
        }, 200);
      }}
      style={{ width: `${sizeBox.width}px`, height: `${sizeBox.height}px` }}
      className={`${tourEventStats.isVisibleFloorplan ? '' : 'hide'} ${
        themeConfig?.selected === 'basekrpano'
          ? 'basekrpano'
          : themeConfig?.selected === 'custom1'
          ? 'custom1'
          : themeConfig?.selected === 'wall'
          ? 'wall'
          : 'default'
      }`}
    >
      {floorplanSelected && (
        <ImageFloorplan
          ref={floorplanRef}
          zoom={1}
          sceneSelected={sceneSelected}
          markerSelected={markerSelected}
          floorplanSelected={floorplanSelected}
          configFloorplan={configFloorplan}
          onChangeScene={onChangeScene}
        />
      )}

      <ActionFloorplanWrapper
        style={{
          zIndex: sizeBox.width === SIZE_BOX.scale.width ? 1 : -1,
          opacity: sizeBox.width === SIZE_BOX.scale.width ? 1 : 0,
        }}
        onMouseOver={e => {
          e.stopPropagation();
          isActionAreaOverRef.current = true;
        }}
        onMouseLeave={e => {
          e.stopPropagation();
          isActionAreaOverRef.current = false;
        }}
      >
        <div className="--left">
          <DropdownContainer
            menuActive={[floorplanSelected?.id || '']}
            listMenu={listFloorplan.map(floorplan => ({
              type: floorplan.id,
              classIcon: 'far fa-layer-group',
              label: floorplan.name,
            }))}
            onClick={onChangeFloorplan}
          >
            <div className="action--item --select">
              <span>{floorplanSelected?.name || '---'}</span>
              <i aria-hidden className="fas fa-caret-down" />
            </div>
          </DropdownContainer>
        </div>
        <div className="--right">
          <div className="action--item" onClick={onZoomIn}>
            <i aria-hidden className="far fa-search-plus" />
          </div>
          <div className="action--item" onClick={onZoomOut}>
            <i aria-hidden className="far fa-search-minus" />
          </div>
          <div className="action--item" onClick={onCenterToMarkerActive}>
            <i aria-hidden className="far fa-crosshairs" />
          </div>
          <div className="action--item" onClick={onOpenPopup}>
            <i aria-hidden className="far fa-expand" />
          </div>
          <div className="action--item" onClick={onCloseFloorplan}>
            <i aria-hidden className="far fa-times" />
          </div>
        </div>
      </ActionFloorplanWrapper>

      {visiblePopup && (
        <Popup
          visible={visiblePopup}
          listFloorplan={listFloorplan}
          markerSelected={markerSelected}
          floorplanSelected={floorplanSelected}
          configFloorplan={configFloorplan}
          onChangeFloorplan={onChangeFloorplan}
          onChangeScene={onChangeScene}
          onCancel={onClosePopup}
        />
      )}
    </FloorplanBoxWrapper>
  );
});

export default FloorplanBox;
